import { useParams } from "react-router-dom";
import YoutubeEmbedVideo from "./YoutubeEmbedVideoRaw";

function getCurrentDimension() {
  const width = window.innerWidth;
  const height = window.innerHeight;
  const isHH = width < 700;
  const videoWidth = isHH ? width + 90 : width - 150;
  const videoHeight = isHH ? height - 300 : height - 150;
  return {
    width,
    height,
    videoWidth,
    videoHeight,
  };
}

export const SinglePlaylist = () => {
  let { playlistId = "" } = useParams();
  const { videoWidth, videoHeight } = getCurrentDimension();

  return (
    <div width={videoWidth} height={videoHeight + 20}>
      <YoutubeEmbedVideo
        playlistId={playlistId}
        width={videoWidth}
        height={videoHeight}
        isSingleVideo={true}
        autoPlay={true}
      />
    </div>
  );
};

export const SingleVideo = () => {
  let { videoId = "" } = useParams();
  const { videoWidth, videoHeight } = getCurrentDimension();
  return (
    <div width={videoWidth} height={videoHeight + 20}>
      <YoutubeEmbedVideo
        videoId={videoId}
        width={videoWidth}
        height={videoHeight}
        isSingleVideo={true}
        autoPlay={true}
      />
    </div>
  );
};
