import React from "react";

const YoutubeEmbedVideoRaw = ({ playlistId = "", videoId = "", width = 500, height, autoPlay=false }) => {
  const isPlaylist = !!playlistId
  const videoHeight = height ? height : width - 150

  if (!videoId && !playlistId) return <></>;
  let fullPath = isPlaylist
    ? `https://www.youtube.com/embed/videoseries?list=${playlistId}`
    : `https://www.youtube.com/embed/${videoId}`;

    
  if (fullPath.indexOf('shorts/') > -1) {
    console.log('##yes short')
    fullPath = isPlaylist
    ? `https://www.youtube.com/embed/${playlistId}`
    : `https://www.youtube.com/embed/${videoId}`;
    fullPath = fullPath.replace('shorts/','');
  } else if (autoPlay) {
    fullPath = isPlaylist ? fullPath + `&autoplay=1` : fullPath + `?autoplay=1`
  }
  console.log('##fullPath', fullPath)

  const iframe = `<iframe width=${width - 120} height=${videoHeight} src=${fullPath}
    title="Study video player" frameborder="0" allow="accelerometer; autoplay;
    clipboard-write; encrypted-media; gyroscope; picture-in-picture;
    web-share" fullscreen allowfullscreen></iframe>`;

  const getIframe = function () {
    return {
      __html: iframe,
    };
  };
  return (
    <div>
      <div dangerouslySetInnerHTML={getIframe()} />
    </div>
  );
};

export default YoutubeEmbedVideoRaw;

// https://developers.google.com/youtube/youtube_player_demo
