import { useEffect, useState } from "react";
import { history } from "containers/RoutesContainer";

import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import Grid from "@mui/material/Unstable_Grid2"; // Grid version 2

import { Button, CardActionArea, CardActions } from "@mui/material";
import YoutubeEmbedVideo from "./YoutubeEmbedVideoRaw";
import Tabs from "./CommonTab";

const loadMoreTimer = 6000;
const width = 420;

const isShortVideo = (id) => id.indexOf('shorts/') > -1


const PlayListsComponent = ({ category }) => {
  const [canShowMore, setCanShow] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => setCanShow(true), loadMoreTimer);
    return () => clearTimeout(timer);
  });
  return (
    <Grid container spacing={4}>
      {category.playLists?.map((playlistId, index) => {
        if (index > 7 && !canShowMore) {
          return <div key={playlistId}></div>;
        }
        return (
          <Grid item xs={12} md={3} key={playlistId}>
            <CardVideo playlistId={playlistId} category={category} />
          </Grid>
        );
      })}
    </Grid>
  );
};

const VideoComponent = ({ category }) => {
  const [canShowMore, setCanShow] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => setCanShow(true), loadMoreTimer);
    return () => clearTimeout(timer);
  });
  return (
    <Grid container spacing={4}>
      {category.videos?.map((videoId, index) => {
        if (index > 7 && !canShowMore) {
          return <div key={videoId}></div>;
        }
        return (
          <Grid item xs={12} md={3} key={videoId}>
            <CardVideo videoId={videoId} category={category} />
          </Grid>
        );
      })}
    </Grid>
  );
};

export default function CardVideoList({ category }) {
  return (
    <Tabs
      tabLabels={["Playlists", "Videos"]}
      tabComponents={[PlayListsComponent, VideoComponent]}
      additionalProps={{ category }}
    />
  );
}

function CardVideo({ playlistId, videoId, category = {} }) {
  const isPlaylist = !!playlistId;
  const isShort = isPlaylist ? isShortVideo(playlistId) : isShortVideo(videoId)

  useEffect(() => {
    var monitor = setInterval(function () {
      var elem = document.activeElement;
      if (elem && elem.tagName === "IFRAME") {
        clearInterval(monitor);
        const src = elem.getAttribute("src");
        // Check to avoid multiple route change trigger for all the Iframes
        if (src.indexOf(playlistId) > -1 || src.indexOf(videoId) > -1) {
          const newRoute = isPlaylist
            ? `/playlist/${playlistId}`
            : `/video/${videoId}`;
          history.replace(newRoute);
        }
      }
    }, 100);
  }, [playlistId, videoId, isPlaylist]);

  const videoHeight = isShort ? 300 : null

  return (
    <Card sx={{ maxWidth: width }}>
      <CardActionArea>
        <CardMedia width={`${width}px`} height={`${videoHeight}px`} alt="Video">
          <YoutubeEmbedVideo
            playlistId={playlistId}
            videoId={videoId}
            width={width}
            height={videoHeight}
            isShort={isShort}
          />
        </CardMedia>
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            {category?.name}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary">
          Share
        </Button>
      </CardActions>
    </Card>
  );
}
